'use client'

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Checkbox } from '@/components/ui/checkbox'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Textarea } from '@/components/ui/textarea'
import { Alert, AlertDescription } from '@/components/ui/alert'

interface FormData {
  name: string
  age: string
  marital_status: string
  gender: string
  nationality: string
  monthly_income: string
  monthly_expense: string
  debt_percentage: string
  active_debts: boolean
  previous_loans: string
  late_payments: string
  active_credit_cards: boolean
  spending_behavior: string
  payment_delay: string
  average_balance: string
}

interface CreditResult {
  name: string
  risk_level: string
  score: number
  explanation: string
  factors: {
    positive: string[]
    negative: string[]
    neutral: string[]
  }
}

export default function CreditScoringPage() {
  const [formData, setFormData] = useState<FormData>({
    name: '',
    age: '',
    marital_status: '',
    gender: '',
    nationality: '',
    monthly_income: '',
    monthly_expense: '',
    debt_percentage: '',
    active_debts: false,
    previous_loans: '',
    late_payments: '',
    active_credit_cards: false,
    spending_behavior: '',
    payment_delay: '',
    average_balance: ''
  })

  const [result, setResult] = useState<CreditResult | null>(null)
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState('')

  const handleInputChange = (field: keyof FormData, value: string | boolean) => {
    setFormData(prev => ({
      ...prev,
      [field]: value
    }))
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    setError('')
    setResult(null)

    try {
      const payload = {
        ...formData,
        age: parseInt(formData.age),
        monthly_income: parseFloat(formData.monthly_income),
        monthly_expense: parseFloat(formData.monthly_expense),
        debt_percentage: parseFloat(formData.debt_percentage),
        previous_loans: parseInt(formData.previous_loans),
        late_payments: parseInt(formData.late_payments),
        payment_delay: formData.payment_delay ? parseFloat(formData.payment_delay) : 0,
        average_balance: formData.average_balance ? parseFloat(formData.average_balance) : 0
      }

      const response = await fetch('http://localhost:8000/score', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(payload)
      })

      if (!response.ok) {
        const errorData = await response.json()
        throw new Error(errorData.detail || 'Error al procesar la solicitud')
      }

      const data = await response.json()
      setResult(data)
    } catch (err: any) {
      setError(err.message || 'Error de conexión con el servidor')
    } finally {
      setLoading(false)
    }
  }

  const getScoreColor = (score: number) => {
    if (score <= 40) return 'text-red-600 dark:text-red-400'
    if (score <= 70) return 'text-yellow-600 dark:text-yellow-400'
    return 'text-green-600 dark:text-green-400'
  }

  const getRiskBadgeColor = (riskLevel: string) => {
    if (riskLevel === 'riesgo alto') return 'bg-red-100 text-red-800 dark:bg-red-900 dark:text-red-200'
    if (riskLevel === 'riesgo medio') return 'bg-yellow-100 text-yellow-800 dark:bg-yellow-900 dark:text-yellow-200'
    return 'bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200'
  }

  return (
    <div className="min-h-screen bg-slate-50 dark:bg-slate-900 py-8">
      <div className="container mx-auto px-4 max-w-4xl">
        <div className="text-center mb-8">
          <h1 className="text-4xl font-bold text-slate-900 dark:text-slate-100 mb-4">
            Evaluación de Crédito
          </h1>
          <p className="text-lg text-slate-600 dark:text-slate-300">
            Complete el formulario para obtener su score crediticio personalizado
          </p>
        </div>

        <div className="grid lg:grid-cols-2 gap-8">
          {/* Form Section */}
          <Card>
            <CardHeader>
              <CardTitle>Información del Solicitante</CardTitle>
              <CardDescription>
                Todos los campos marcados con * son obligatorios
              </CardDescription>
            </CardHeader>
            <CardContent>
              <form onSubmit={handleSubmit} className="space-y-6">
                {/* Personal Information */}
                <div className="space-y-4">
                  <h3 className="text-lg font-semibold text-slate-900 dark:text-slate-100">
                    Datos Personales
                  </h3>
                  
                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <Label htmlFor="name">Nombre Completo *</Label>
                      <Input
                        id="name"
                        value={formData.name}
                        onChange={(e) => handleInputChange('name', e.target.value)}
                        required
                        placeholder="Juan Pérez"
                      />
                    </div>
                    <div>
                      <Label htmlFor="age">Edad *</Label>
                      <Input
                        id="age"
                        type="number"
                        value={formData.age}
                        onChange={(e) => handleInputChange('age', e.target.value)}
                        required
                        min="18"
                        max="100"
                      />
                    </div>
                  </div>

                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <Label htmlFor="marital_status">Estado Civil *</Label>
                      <Select onValueChange={(value) => handleInputChange('marital_status', value)}>
                        <SelectTrigger>
                          <SelectValue placeholder="Seleccionar" />
                        </SelectTrigger>
                        <SelectContent>
                          <SelectItem value="soltero">Soltero/a</SelectItem>
                          <SelectItem value="casado">Casado/a</SelectItem>
                          <SelectItem value="divorciado">Divorciado/a</SelectItem>
                          <SelectItem value="viudo">Viudo/a</SelectItem>
                        </SelectContent>
                      </Select>
                    </div>
                    <div>
                      <Label htmlFor="gender">Género</Label>
                      <Select onValueChange={(value) => handleInputChange('gender', value)}>
                        <SelectTrigger>
                          <SelectValue placeholder="Opcional" />
                        </SelectTrigger>
                        <SelectContent>
                          <SelectItem value="masculino">Masculino</SelectItem>
                          <SelectItem value="femenino">Femenino</SelectItem>
                          <SelectItem value="otro">Otro</SelectItem>
                        </SelectContent>
                      </Select>
                    </div>
                  </div>

                  <div>
                    <Label htmlFor="nationality">Nacionalidad *</Label>
                    <Input
                      id="nationality"
                      value={formData.nationality}
                      onChange={(e) => handleInputChange('nationality', e.target.value)}
                      required
                      placeholder="Argentina"
                    />
                  </div>
                </div>

                {/* Financial Information */}
                <div className="space-y-4">
                  <h3 className="text-lg font-semibold text-slate-900 dark:text-slate-100">
                    Información Financiera
                  </h3>
                  
                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <Label htmlFor="monthly_income">Ingresos Mensuales * ($)</Label>
                      <Input
                        id="monthly_income"
                        type="number"
                        value={formData.monthly_income}
                        onChange={(e) => handleInputChange('monthly_income', e.target.value)}
                        required
                        min="0"
                        step="0.01"
                        placeholder="50000"
                      />
                    </div>
                    <div>
                      <Label htmlFor="monthly_expense">Gastos Mensuales * ($)</Label>
                      <Input
                        id="monthly_expense"
                        type="number"
                        value={formData.monthly_expense}
                        onChange={(e) => handleInputChange('monthly_expense', e.target.value)}
                        required
                        min="0"
                        step="0.01"
                        placeholder="30000"
                      />
                    </div>
                  </div>

                  <div>
                    <Label htmlFor="debt_percentage">Porcentaje de Endeudamiento * (%)</Label>
                    <Input
                      id="debt_percentage"
                      type="number"
                      value={formData.debt_percentage}
                      onChange={(e) => handleInputChange('debt_percentage', e.target.value)}
                      required
                      min="0"
                      max="100"
                      step="0.1"
                      placeholder="25"
                    />
                  </div>

                  <div className="flex items-center space-x-2">
                    <Checkbox
                      id="active_debts"
                      checked={formData.active_debts}
                      onCheckedChange={(checked) => handleInputChange('active_debts', checked as boolean)}
                    />
                    <Label htmlFor="active_debts">¿Tiene deudas activas?</Label>
                  </div>
                </div>

                {/* Credit History */}
                <div className="space-y-4">
                  <h3 className="text-lg font-semibold text-slate-900 dark:text-slate-100">
                    Historial Crediticio
                  </h3>
                  
                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <Label htmlFor="previous_loans">Préstamos Anteriores *</Label>
                      <Input
                        id="previous_loans"
                        type="number"
                        value={formData.previous_loans}
                        onChange={(e) => handleInputChange('previous_loans', e.target.value)}
                        required
                        min="0"
                        placeholder="2"
                      />
                    </div>
                    <div>
                      <Label htmlFor="late_payments">Pagos Tardíos *</Label>
                      <Input
                        id="late_payments"
                        type="number"
                        value={formData.late_payments}
                        onChange={(e) => handleInputChange('late_payments', e.target.value)}
                        required
                        min="0"
                        placeholder="0"
                      />
                    </div>
                  </div>

                  <div className="flex items-center space-x-2">
                    <Checkbox
                      id="active_credit_cards"
                      checked={formData.active_credit_cards}
                      onCheckedChange={(checked) => handleInputChange('active_credit_cards', checked as boolean)}
                    />
                    <Label htmlFor="active_credit_cards">¿Tiene tarjetas de crédito activas?</Label>
                  </div>
                </div>

                {/* Behavioral Information */}
                <div className="space-y-4">
                  <h3 className="text-lg font-semibold text-slate-900 dark:text-slate-100">
                    Comportamiento Financiero
                  </h3>
                  
                  <div>
                    <Label htmlFor="spending_behavior">¿En qué gasta más? *</Label>
                    <Input
                      id="spending_behavior"
                      value={formData.spending_behavior}
                      onChange={(e) => handleInputChange('spending_behavior', e.target.value)}
                      required
                      placeholder="alimentación, entretenimiento, transporte..."
                    />
                  </div>

                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <Label htmlFor="payment_delay">Retraso Promedio en Pagos (días)</Label>
                      <Input
                        id="payment_delay"
                        type="number"
                        value={formData.payment_delay}
                        onChange={(e) => handleInputChange('payment_delay', e.target.value)}
                        min="0"
                        step="0.1"
                        placeholder="0"
                      />
                    </div>
                    <div>
                      <Label htmlFor="average_balance">Saldo Promedio en Cuenta ($)</Label>
                      <Input
                        id="average_balance"
                        type="number"
                        value={formData.average_balance}
                        onChange={(e) => handleInputChange('average_balance', e.target.value)}
                        min="0"
                        step="0.01"
                        placeholder="10000"
                      />
                    </div>
                  </div>
                </div>

                <Button 
                  type="submit" 
                  className="w-full" 
                  disabled={loading}
                >
                  {loading ? 'Calculando Score...' : 'Calcular Score de Crédito'}
                </Button>
              </form>
            </CardContent>
          </Card>

          {/* Results Section */}
          <div className="space-y-6">
            {error && (
              <Alert className="border-red-200 bg-red-50 dark:border-red-800 dark:bg-red-950">
                <AlertDescription className="text-red-800 dark:text-red-200">
                  {error}
                </AlertDescription>
              </Alert>
            )}

            {result && (
              <Card>
                <CardHeader>
                  <CardTitle className="flex items-center justify-between">
                    Resultado del Análisis
                    <span className={`text-3xl font-bold ${getScoreColor(result.score)}`}>
                      {result.score}/100
                    </span>
                  </CardTitle>
                </CardHeader>
                <CardContent className="space-y-6">
                  <div className="text-center">
                    <h3 className="text-2xl font-bold text-slate-900 dark:text-slate-100 mb-2">
                      {result.name}
                    </h3>
                    <span className={`inline-block px-4 py-2 rounded-full text-sm font-semibold ${getRiskBadgeColor(result.risk_level)}`}>
                      {result.risk_level.toUpperCase()}
                    </span>
                  </div>

                  <div className="space-y-4">
                    <div className="bg-slate-50 dark:bg-slate-800 p-4 rounded-lg">
                      <h4 className="font-semibold text-slate-900 dark:text-slate-100 mb-2">
                        Explicación Detallada
                      </h4>
                      <pre className="text-sm text-slate-600 dark:text-slate-300 whitespace-pre-wrap font-sans">
                        {result.explanation}
                      </pre>
                    </div>

                    {result.factors.positive.length > 0 && (
                      <div className="bg-green-50 dark:bg-green-950 p-4 rounded-lg">
                        <h4 className="font-semibold text-green-800 dark:text-green-200 mb-2">
                          ✅ Factores Positivos
                        </h4>
                        <ul className="text-sm text-green-700 dark:text-green-300 space-y-1">
                          {result.factors.positive.map((factor, index) => (
                            <li key={index}>• {factor}</li>
                          ))}
                        </ul>
                      </div>
                    )}

                    {result.factors.negative.length > 0 && (
                      <div className="bg-red-50 dark:bg-red-950 p-4 rounded-lg">
                        <h4 className="font-semibold text-red-800 dark:text-red-200 mb-2">
                          ⚠️ Factores de Riesgo
                        </h4>
                        <ul className="text-sm text-red-700 dark:text-red-300 space-y-1">
                          {result.factors.negative.map((factor, index) => (
                            <li key={index}>• {factor}</li>
                          ))}
                        </ul>
                      </div>
                    )}

                    {result.factors.neutral.length > 0 && (
                      <div className="bg-blue-50 dark:bg-blue-950 p-4 rounded-lg">
                        <h4 className="font-semibold text-blue-800 dark:text-blue-200 mb-2">
                          ℹ️ Consideraciones Adicionales
                        </h4>
                        <ul className="text-sm text-blue-700 dark:text-blue-300 space-y-1">
                          {result.factors.neutral.map((factor, index) => (
                            <li key={index}>• {factor}</li>
                          ))}
                        </ul>
                      </div>
                    )}
                  </div>

                  <Button 
                    onClick={() => {
                      setResult(null)
                      setFormData({
                        name: '',
                        age: '',
                        marital_status: '',
                        gender: '',
                        nationality: '',
                        monthly_income: '',
                        monthly_expense: '',
                        debt_percentage: '',
                        active_debts: false,
                        previous_loans: '',
                        late_payments: '',
                        active_credit_cards: false,
                        spending_behavior: '',
                        payment_delay: '',
                        average_balance: ''
                      })
                    }}
                    variant="outline"
                    className="w-full"
                  >
                    Nueva Evaluación
                  </Button>
                </CardContent>
              </Card>
            )}

            {!result && !error && (
              <Card>
                <CardHeader>
                  <CardTitle>Información del Sistema</CardTitle>
                </CardHeader>
                <CardContent className="space-y-4">
                  <div className="text-sm text-slate-600 dark:text-slate-300 space-y-2">
                    <p><strong>Algoritmo:</strong> Random Forest + Machine Learning</p>
                    <p><strong>Factores evaluados:</strong> 11+ variables financieras</p>
                    <p><strong>Precisión:</strong> Modelo entrenado con 1000+ casos</p>
                    <p><strong>Tiempo de respuesta:</strong> Instantáneo</p>
                  </div>
                  
                  <div className="bg-slate-50 dark:bg-slate-800 p-4 rounded-lg">
                    <h4 className="font-semibold text-slate-900 dark:text-slate-100 mb-2">
                      Clasificación de Riesgo
                    </h4>
                    <div className="space-y-1 text-sm">
                      <div className="flex justify-between">
                        <span>0-40 puntos:</span>
                        <span className="text-red-600 dark:text-red-400 font-semibold">Riesgo Alto</span>
                      </div>
                      <div className="flex justify-between">
                        <span>41-70 puntos:</span>
                        <span className="text-yellow-600 dark:text-yellow-400 font-semibold">Riesgo Medio</span>
                      </div>
                      <div className="flex justify-between">
                        <span>71-100 puntos:</span>
                        <span className="text-green-600 dark:text-green-400 font-semibold">Bajo Riesgo</span>
                      </div>
                    </div>
                  </div>
                </CardContent>
              </Card>
            )}
          </div>
        </div>
      </div>
    </div>
  )
}
